/* Fix visible slider checkbox */
input {
  -webkit-transform: scale(0.5);
          transform: scale(0.5); }

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s; }

.slider::before {
  position: absolute;
  content: "";
  height: 26px;
  width: 28px;
  left: 0;
  bottom: 0;
  background-color: #ffffff;
  transition: 0.4s; }

input:checked + .slider {
  background-color: #2196f3; }

input:focus + .slider {
  box-shadow: #2196f3; }

input:checked + .slider::before,
input:checked + .slider > .emoji {
  -webkit-transform: translateX(26px);
  transform: translateX(26px); }

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
  display: flex;
  align-items: center; }

.slider.round::before {
  border-radius: 50%;
  display: grid;
  place-items: center; }

.slider::after {
  display: inline;
  position: absolute;
  right: 9.3%;
  bottom: 17.5%; }

input:checked + .slider::after {
  display: inline;
  position: absolute;
  right: 56.3%;
  bottom: 14.5%; }

.emoji {
  z-index: 10;
  display: flex;
  align-items: center;
  margin-left: 2px; }

@media all and (max-width: 786px) and (min-width: 425px) {
  input:checked + .slider::after {
    right: 60.3%;
    bottom: 20.5%; }
  .slider::after {
    right: 15.3%;
    bottom: 22.5%; } }

@media all and (max-width: 2560px) and (min-width: 1552px) {
  .slider::after {
    bottom: 12.5% !important; } }

@media all and (max-width: 1552px) and (min-width: 1440px) {
  .slider::after {
    bottom: 7.5% !important; } }

