  font-weight: 100 !important; }

/* Change colors in this file to style to your needs on a global level. 
Certain colors are used for different and multiply types of elements. 
For greater control over colors visit each components .scss file. */
.contact-div-main {
  display: flex; }

.dark-mode a {
  color: #ffffff; }

.contact-div-main > * {
  flex: 1 1; }

.contact-title {
  font-size: 65px;
  font-weight: 400; }

.contact-detail,
.contact-detail-email {
  margin-top: 20px;
  font-size: 40px;
  font-weight: 400;
  color: #868e96;
  text-decoration: none; }

.contact-detail:hover,
.contact-detail-email:hover {
  color: #000000;
  text-shadow: 2px 1px 2px #b5b5b5;
  transition: all 0.3s; }

.contact-subtitle {
  text-transform: uppercase; }

.contact-text-div {
  margin-top: 1.5rem; }

.contact-margin-top {
  margin-top: 4rem; }

.contact-image-div > img {
  max-width: 100%;
  height: auto;
  margin-left: 1.5rem;
  margin-top: -4rem; }

/* Media Query */
@media (max-width: 1380px), (max-width: 768px) {
  .contact-title {
    font-size: 56px;
    font-weight: 400;
    text-align: center; }
  .contact-title {
    font-size: 40px;
    text-align: center; }
  .contact-subtitle {
    font-size: 16px;
    line-height: normal;
    text-align: center; }
  .contact-text-div {
    text-align: center;
    margin-top: 1.5rem; }
  .contact-detail,
  .contact-detail-email {
    font-size: 20px; }
  .contact-image-div {
    display: none; } }

.splash-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.splash-animation-container {
  height: 50vh;
}

.splash-title-container {
  font-size: 2.5rem;
  text-decoration: none;
}
.splash-title {
  font-family: "Agustina Regular", cursive;
  font-weight: bold;
  -webkit-font-feature-settings: "liga", "clig off";
          font-feature-settings: "liga", "clig off";
  font-variant-ligatures: no-common-ligatures;
  -webkit-font-variant-ligatures: no-common-ligatures;
  padding: 0 20px;
}

/* Change colors in this file to style to your needs on a global level. 
Certain colors are used for different and multiply types of elements. 
For greater control over colors visit each components .scss file. */
.dark-mode {
  background-color: #171c28;
  color: white;
  transition: "0.1s"; }

